// React
import { useState, useEffect } from "react"
// Bootstrap UI
import { Button, Container, Card, Form, Row, Col, Tab, Nav } from 'react-bootstrap'
// 
import { parseKle } from "./KLEParser"        // KLE
import { buildPlate } from "./PlateBuilder"   // 
import Decimal from "decimal.js"              // 
import makerjs from 'makerjs'                 // 
import fileDownload from 'js-file-download'   // 
import { DataHelpPane, SwitchCutoutPane, OtherCutoutPane } from './HelpPanes'  // 

/**
 *  - 
 * SVGDXF
 */
function App() {
  // KLEKeyboard Layout Editor
  const [kleText, setKleText] = useState(`[
  [{a:7},"","","","","","","","","","","",""],
["","","","","","","","","","","",""],
["","","","","","","","","","","",""],
[{w:1.25},"","","","",{w:1.25},"","",{w:1.25},"","","","",{w:1.25},""]
]`)
  // 
  const [previewSvg, setPreviewSvg] = useState(null)  // SVG
  const [plateSvg, setPlateSvg] = useState(null)      // SVG
  const [plateDxf, setPlateDxf] = useState(null)      // DXF
  // 
  const [switchHeight, setSwitchHeight] = useState(14)                    // (mm)
  const [switchWidth, setSwitchWidth] = useState(14)                      // (mm)
  const [switchRadius, setSwitchRadius] = useState(0.5)                   // (mm)
  const [switchDogbone, setSwitchDogbone] = useState(true)                // 
  const [switchPattern, setSwitchPattern] = useState(true)                // 
  const [switchLineWidth, setSwitchLineWidth] = useState(0.5)             // (mm)
  const [switchSolderMaskExpansion, setSwitchSolderMaskExpansion] = useState(0.051)  // (mm)

  // 
  const [stabilizerCutoutType, setStabilizerCutoutType] = useState("mx-basic")  // 
  const [stabilizerRadius, setStabilizerRadius] = useState(0.5)                 // (mm)

  const [stabilizerPattern, setStabilizerPattern] = useState(true)              // 
  const [stabilizerLineWidth, setStabilizerLineWidth] = useState(0.5)           // (mm)
  const [stabilizerSolderMaskExpansion, setStabilizerSolderMaskExpansion] = useState(0.051)  // (mm)

  // 
  const [acousticCutoutType, setAcousticCutoutType] = useState("none")          // 
  const [acousticRadius, setAcousticRadius] = useState(0.5)                     // (mm)
  const [acousticPattern] = useState(true)                  // 
  const [slotWidth, setSlotWidth] = useState(1)                                 // (mm)
  const [switchHousingWidth, setSwitchHousingWidth] = useState(1)               // (mm)
  const [connectorWidth, setConnectorWidth] = useState(1.2)                     // (mm)
  const [extremeSlot, setExtremeSlot] = useState(true)                          // 
  const [slotPattern, setSlotPattern] = useState(true)                          // 
  const [slotLineWidth, setSlotLineWidth] = useState(0.5)                       // (mm)
  const [slotSolderMaskExpansion, setSlotSolderMaskExpansion] = useState(0.051) // (mm)
  const [slotPatternRadius, setSlotPatternRadius] = useState(0.25)              // (mm)
  const [acousticLineWidth] = useState(0.5)               // (mm)
  const [acousticSolderMaskExpansion] = useState(0.051)  // (mm)

  // 
  const [plateRadius, setPlateRadius] = useState(1)                            // (mm)
  const [platePattern, setPlatePattern] = useState(true)                       // 
  const [plateLineWidth, setPlateLineWidth] = useState(1)                      // (mm)
  const [plateSolderMaskExpansion, setPlateSolderMaskExpansion] = useState(0.051)  // (mm)

  const [platePatternRadius, setPlatePatternRadius] = useState(0.5)            // (mm)

  // 
  const [unitWidth, setUnitWidth] = useState(19.05)   // (mm)19.05mm
  const [unitHeight, setUnitHeight] = useState(19.05) // (mm)19.05mm

  // 
  useEffect(() => {
    // KLE
    const kleReturn = parseKle(kleText)

    // 
    if (kleReturn && kleReturn.length > 0) {
      try {
        // 
        const plateData = buildPlate(kleReturn, {
          // 
          switchHeight: new Decimal(switchHeight),                              // 
          switchWidth: new Decimal(switchWidth),                                // 
          switchFilletRadius: new Decimal(switchRadius),                        // 
          switchDogbone: switchDogbone,                                         // 
          switchPattern: switchPattern,                                         // 
          switchLineWidth: new Decimal(switchLineWidth),                        // 
          switchSolderMaskExpansion: new Decimal(switchSolderMaskExpansion),    // 

          // 
          stabilizerCutoutType: stabilizerCutoutType,                           // 
          stabilizerFilletRadius: new Decimal(stabilizerRadius),                // 

          stabilizerPattern: stabilizerPattern,                                 // 
          stabilizerLineWidth: new Decimal(stabilizerLineWidth),                // 
          stabilizerSolderMaskExpansion: new Decimal(stabilizerSolderMaskExpansion), // 

          // 
          acousticCutoutType: acousticCutoutType,                               // 
          acousticFilletRadius: new Decimal(acousticRadius),                    // 
          acousticPattern: acousticPattern,                                     // 
          acousticLineWidth: new Decimal(acousticLineWidth),                    // 
          acousticSolderMaskExpansion: new Decimal(acousticSolderMaskExpansion), // 

          // 
          plateRadius: new Decimal(plateRadius),                                // 
          platePattern: platePattern,                                           // 
          plateLineWidth: new Decimal(plateLineWidth),                          // 
          plateSolderMaskExpansion: new Decimal(plateSolderMaskExpansion),      // 

          platePatternRadius: new Decimal(platePatternRadius),                  // 

          // 
          unitWidth: new Decimal(unitWidth),                                    // 
          unitHeight: new Decimal(unitHeight),                                  // 

          // 
          slotWidth: new Decimal(slotWidth),                                    // 
          switchHousingWidth: new Decimal(switchHousingWidth),                  // 
          connectorWidth: new Decimal(connectorWidth),                          // 
          extremeSlot: extremeSlot,                                             // 
          slotPattern: slotPattern,                                             // 
          slotLineWidth: new Decimal(slotLineWidth),                            // 
          slotSolderMaskExpansion: new Decimal(slotSolderMaskExpansion),        // 
          slotPatternRadius: new Decimal(slotPatternRadius),                    // 
        })

        // SVG
        const previewSvgData = makerjs.exporter.toSVG(plateData, {
          stroke: 'white',           // 
          strokeWidth: '0.5mm',      // 
          svgAttrs: {
            width: '100%',           // 
            height: '100%'           // 
          }
        })
        setPreviewSvg(previewSvgData)

        // SVG
        const svgData = makerjs.exporter.toSVG(plateData, {
          units: makerjs.unitType.Millimeter  // 
        })
        setPlateSvg(svgData)

        // DXFCAD
        const dxfData = makerjs.exporter.toDXF(plateData, {
          units: makerjs.unitType.Millimeter,  // 
          layerOptions: {
            plate: { color: 6 },              // 
            top: { color: 1 },                // 
            bottom: { color: 5 },             // 
            topSolderMask: { color: 2 },      // 
            bottomSolderMask: { color: 3 }    // 
          }
        })
        setPlateDxf(dxfData)

      } catch (error) {
        // 
        console.log(error)
        setPreviewSvg(null)
        setPlateSvg(null)
        setPlateDxf(null)
      }
    } else {
      // KLE
      setPreviewSvg(null)
      setPlateSvg(null)
      setPlateDxf(null)
    }

  }, [
    // useEffect
    kleText,                          // KLE
    switchHeight,                     // 
    switchWidth,                      // 
    switchRadius,                     // 
    switchDogbone,                    // 
    switchPattern,                    // 
    switchLineWidth,                  // 
    switchSolderMaskExpansion,        // 
    stabilizerCutoutType,             // 
    stabilizerRadius,                 // 

    stabilizerPattern,                // 
    stabilizerLineWidth,              // 
    stabilizerSolderMaskExpansion,    // 
    acousticCutoutType,               // 
    acousticRadius,                   // 
    acousticPattern,                  // 
    slotWidth,                        // 
    switchHousingWidth,               // 
    plateRadius,                      // 
    platePattern,                     // 
    plateLineWidth,                   // 
    plateSolderMaskExpansion,         // 

    unitWidth,                        // 
    unitHeight,                       // 
    platePatternRadius,               // 
    connectorWidth,                   // 
    extremeSlot,                      // 
    slotPattern,                      // 
    slotLineWidth,                    // 
    slotSolderMaskExpansion,          // 
    slotPatternRadius,                // 
    acousticLineWidth,                // 
    acousticSolderMaskExpansion       // 
  ])

  // 
  useEffect(() => {
    // 
    if (acousticRadius > slotWidth) {
      setAcousticRadius(slotWidth)  // 
    }
  }, [slotWidth, acousticRadius, setAcousticRadius])  // 

  /**
   * 
   * @param {string} fileData - 
   * @param {string} extension - .svg, .dxf
   */
  const downloadData = (fileData, extension) => {
    const date = new Date(Date.now())  // 
    // 
    fileDownload(fileData, "plate-" + date.toISOString() + extension)
  }

  // 
  return (
    <Container className="App justify-content-center" style={{ textAlign: "center", maxWidth: "1200px" }}>
      {/*  */}
      <div className="pt-4 pb-4">
      </div>

      {/* KLE */}
      <Card className="rounded shadow overflow-hidden mb-5">
        <Card.Body className="p-0">
          <Row>
            <Col xl={5} className="pt-3 pb-0 ps-4 pe-4">
              <h3>KLE </h3>
              <p></p>
            </Col>
            <Col xl={7}>
              <Form>
                <Form.Control
                  as="textarea"
                  type="text"
                  style={{
                    fontFamily: 'monospace',  // JSON
                    height: '20vh',           // 20%
                    minHeight: "225px"        // 225
                  }}
                  spellCheck="false"          // 
                  placeholder=" KLE  JSON"
                  onChange={text => setKleText(text.target.value)}  // 
                />
              </Form>
            </Col>
          </Row>
        </Card.Body>
      </Card>

      {/*  */}
      <Card className="rounded shadow overflow-hidden mb-5">
        <Card.Body>
          <Row>
            {/*  */}
            <Col lg={3}>
              <h3></h3>
              <Form className="ms-3 me-3">
                <Row>
                  <Col xs={6}>
                    <Form.Label> (mm)</Form.Label>
                    <Form.Control
                      type="number"
                      required
                      step="0.01"
                      min="0"
                      max="50"
                      defaultValue="14"
                      className="mb-3"
                      onChange={e => setSwitchHeight(parseFloat(e.target.value))}
                    />
                  </Col>
                  <Col xs={6}>
                    <Form.Label> (mm)</Form.Label>
                    <Form.Control
                      type="number"
                      required
                      step="0.01"
                      min="0"
                      max="100"
                      defaultValue="19.05"
                      className="mb-3"
                      onChange={e => setUnitHeight(parseFloat(e.target.value))}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col xs={6}>
                    <Form.Label> (mm)</Form.Label>
                    <Form.Control
                      type="number"
                      required
                      step="0.01"
                      min="0"
                      max="50"
                      defaultValue="14"
                      className="mb-3"
                      onChange={e => setSwitchWidth(parseFloat(e.target.value))}
                    />
                  </Col>
                  <Col xs={6}>
                    <Form.Label> (mm)</Form.Label>
                    <Form.Control
                      type="number"
                      required
                      step="0.01"
                      min="0"
                      max="100"
                      defaultValue="19.05"
                      className="mb-3"
                      onChange={e => setUnitWidth(parseFloat(e.target.value))}
                    />
                  </Col>
                </Row>
                <Form.Label> (mm)</Form.Label>
                <Form.Control
                  type="number"
                  required
                  step="0.1"
                  min="0"
                  max="10"
                  defaultValue="0.5"
                  className="mb-3"
                  onChange={e => setSwitchRadius(parseFloat(e.target.value))}
                />
                <Row>
                  <Col xs={6}>
                    <Form.Check
                      type="checkbox"
                      id="switch-dogbone"
                      label=""
                      defaultChecked={true}
                      className="mb-3"
                      onChange={e => setSwitchDogbone(e.target.checked)}
                    />
                  </Col>
                </Row>
              </Form>
            </Col>

            {/*  */}
            <Col lg={3}>
              <h3></h3>
              <Form className="ms-3 me-3">
                <Form.Label></Form.Label>
                <Form.Select
                  aria-label="stabilizer-cutout-type"
                  selected="mx-basic"
                  className="mb-3"
                  onChange={e => setStabilizerCutoutType(e.target.value)}
                >
                  <option value="mx-basic">Cherry MX </option>
                  <option value="mx-small">Cherry MX </option>
                  <option value="mx-spec">Cherry MX </option>
                  <option value="alps-aek">Alps AEK</option>
                  <option value="alps-at101">Alps AT101</option>
                  <option value="none"></option>
                </Form.Select>

                <Form.Label> (mm)</Form.Label>
                <Form.Control
                  type="number"
                  required
                  step="0.1"
                  min="0"
                  max="10"
                  defaultValue="0.5"
                  className="mb-3"
                  onChange={e => setStabilizerRadius(parseFloat(e.target.value))}
                />


              </Form>
            </Col>

            {/*  */}
            <Col lg={3}>
              <h3></h3>
              <Form className="ms-3 me-3">
                <Form.Label></Form.Label>
                <Form.Select
                  aria-label="acoustic-cutout-type"
                  selected="none"
                  className="mb-3"
                  onChange={e => setAcousticCutoutType(e.target.value)}
                >
                  <option value="none"></option>
                  <option value="mx-basic">Cherry MX </option>
                  <option value="mx-extreme">Cherry MX </option>
                </Form.Select>

                <Form.Check
                  type="checkbox"
                  id="extreme-slot"
                  label=""
                  checked={extremeSlot}
                  className="mb-3"
                  onChange={e => setExtremeSlot(e.target.checked)}
                />

                {extremeSlot && (
                  <>
                    <Row>
                      <Col xs={6}>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.1"
                          min="0.1"
                          max="10"
                          defaultValue="1"
                          className="mb-3"
                          onChange={e => setSlotWidth(parseFloat(e.target.value))}
                        />
                      </Col>
                      <Col xs={6}>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.1"
                          min="0"
                          max={slotWidth / 2}
                          defaultValue={slotWidth / 2}
                          value={acousticRadius}
                          className="mb-3"
                          onChange={e => setAcousticRadius(parseFloat(e.target.value))}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col xs={6}>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.1"
                          min="0.1"
                          max="10"
                          defaultValue="1"
                          className="mb-3"
                          onChange={e => setSwitchHousingWidth(parseFloat(e.target.value))}
                        />
                      </Col>
                      <Col xs={6}>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.01"
                          min="0.1"
                          max="10"
                          defaultValue="1.2"
                          className="mb-3"
                          onChange={e => setConnectorWidth(parseFloat(e.target.value))}
                        />
                      </Col>
                    </Row>
                  </>
                )}
              </Form>
            </Col>

            {/*  */}
            <Col lg={3}>
              <h3></h3>
              <Form className="ms-3 me-3">
                <Form.Label> (mm)</Form.Label>
                <Form.Control
                  type="number"
                  required
                  step="0.1"
                  min="0"
                  max="10"
                  value={plateRadius}
                  className="mb-3"
                  onChange={e => setPlateRadius(parseFloat(e.target.value))}
                />

              </Form>
            </Col>
          </Row>
        </Card.Body>
      </Card>

      {/*  */}
      <Card className="rounded shadow overflow-hidden mb-5">
        <Card.Body>
          <Row>
            <Col lg={12}>
              <Form className="ms-3 me-3">
                <Row>
                  {/*  */}
                  <Col lg={3}>
                    <h5></h5>
                    <Form.Check
                      type="checkbox"
                      id="switch-pattern"
                      label=""
                      checked={switchPattern}
                      className="mb-3"
                      onChange={e => setSwitchPattern(e.target.checked)}
                    />
                    {switchPattern && (
                      <>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="5"
                          defaultValue="0.5"
                          className="mb-3"
                          onChange={e => setSwitchLineWidth(parseFloat(e.target.value))}
                        />
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="1"
                          defaultValue="0.051"
                          className="mb-3"
                          onChange={e => setSwitchSolderMaskExpansion(parseFloat(e.target.value))}
                        />
                      </>
                    )}
                  </Col>
                  {/*  */}
                  <Col lg={3}>
                    <h5></h5>
                    <Form.Check
                      type="checkbox"
                      id="stabilizer-pattern"
                      label=""
                      checked={stabilizerPattern}
                      className="mb-3"
                      onChange={e => setStabilizerPattern(e.target.checked)}
                    />
                    {stabilizerPattern && (
                      <>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="5"
                          defaultValue="0.5"
                          className="mb-3"
                          onChange={e => setStabilizerLineWidth(parseFloat(e.target.value))}
                        />
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="1"
                          defaultValue="0.051"
                          className="mb-3"
                          onChange={e => setStabilizerSolderMaskExpansion(parseFloat(e.target.value))}
                        />
                      </>
                    )}
                  </Col>
                  {/*  */}
                  <Col lg={3}>
                    <h5></h5>
                    <Form.Check
                      type="checkbox"
                      id="slot-pattern"
                      label=""
                      checked={slotPattern}
                      className="mb-3"
                      onChange={e => setSlotPattern(e.target.checked)}
                    />
                    {slotPattern && (
                      <>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="5"
                          defaultValue="0.5"
                          className="mb-3"
                          onChange={e => setSlotLineWidth(parseFloat(e.target.value))}
                        />
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="1"
                          defaultValue="0.051"
                          className="mb-3"
                          onChange={e => setSlotSolderMaskExpansion(parseFloat(e.target.value))}
                        />
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.01"
                          min="0"
                          max="10"
                          defaultValue="0.25"
                          className="mb-3"
                          onChange={e => setSlotPatternRadius(parseFloat(e.target.value))}
                        />
                      </>
                    )}
                  </Col>
                  {/*  */}
                  <Col lg={3}>
                    <h5></h5>
                    <Form.Check
                      type="checkbox"
                      id="plate-pattern"
                      label=""
                      checked={platePattern}
                      className="mb-3"
                      onChange={e => setPlatePattern(e.target.checked)}
                    />
                    {platePattern && (
                      <>
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="5"
                          defaultValue="1"
                          className="mb-3"
                          onChange={e => setPlateLineWidth(parseFloat(e.target.value))}
                        />
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.001"
                          min="0"
                          max="1"
                          defaultValue="0.051"
                          className="mb-3"
                          onChange={e => setPlateSolderMaskExpansion(parseFloat(e.target.value))}
                        />
                        <Form.Label> (mm)</Form.Label>
                        <Form.Control
                          type="number"
                          required
                          step="0.01"
                          min="0"
                          max="10"
                          defaultValue="0.5"
                          className="mb-3"
                          onChange={e => setPlatePatternRadius(parseFloat(e.target.value))}
                        />
                      </>
                    )}
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
        </Card.Body>
      </Card>

      {/*  */}
      <Card className="p-0 rounded shadow bg-dark mb-5 overflow-hidden">
        {/* SVG */}
        <Card.Body
          className="bg-dark p-4"
          style={{ height: "30vh", minHeight: "250px" }}
          dangerouslySetInnerHTML={{ __html: previewSvg }}  // SVG HTML
        />
        {/*  */}
        <Card.Footer>
          {/*  */}
          {plateDxf &&
            <Button variant="light" className="me-3" onClick={() => { downloadData(plateDxf, ".dxf") }}>
               DXF
            </Button>
          }
          {plateSvg &&
            <Button variant="light" onClick={() => { downloadData(plateSvg, ".svg") }}>
               SVG
            </Button>
          }
          {/*  */}
          {!plateDxf &&
            <Button variant="light" className="me-3" disabled> DXF</Button>
          }
          {!plateSvg &&
            <Button variant="light" disabled> SVG</Button>
          }
        </Card.Footer>
      </Card>

      {/*  */}
      <Card className="rounded shadow overflow-hidden mb-5">
        <Tab.Container id="left-tabs-example" defaultActiveKey="data">
          <Row>
            {/*  */}
            <Col lg={4} as={Card.Header}>
              <Nav variant="pills" className="flex-column">
                <Nav.Item className="me-0">
                  <Nav.Link eventKey="data"></Nav.Link>
                </Nav.Item>
                <Nav.Item className="me-0">
                  <Nav.Link eventKey="switch-cutout"></Nav.Link>
                </Nav.Item>
                <Nav.Item className="me-0">
                  <Nav.Link eventKey="stabilizer-cutout"></Nav.Link>
                </Nav.Item>
                <Nav.Item className="me-0">
                  <Nav.Link eventKey="acoustic-cutout"></Nav.Link>
                </Nav.Item>
                <Nav.Item className="me-0">
                  <Nav.Link eventKey="plate-settings"></Nav.Link>
                </Nav.Item>
              </Nav>
            </Col>
            {/*  */}
            <Col lg={8} as={Card.Body} className="p-4">
              <Tab.Content>
                <Tab.Pane eventKey="data" style={{ textAlign: "left" }}>
                  <DataHelpPane />
                </Tab.Pane>
                <Tab.Pane eventKey="switch-cutout" style={{ textAlign: "left" }}>
                  <SwitchCutoutPane />
                </Tab.Pane>
                <Tab.Pane eventKey="stabilizer-cutout" style={{ textAlign: "left" }}>
                  <OtherCutoutPane />
                </Tab.Pane>
                <Tab.Pane eventKey="acoustic-cutout" style={{ textAlign: "left" }}>
                  <OtherCutoutPane />
                </Tab.Pane>
                <Tab.Pane eventKey="plate-settings" style={{ textAlign: "left" }}>
                  <OtherCutoutPane />
                </Tab.Pane>
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
      </Card>

      {/*  */}
      <br />
    </Container>
  );
}

export default App;
